<template>
  <div class="manage-problem">
    <transition name="el-fade-in-linear">
      <div v-show="!isShowEdit">
        <div class="handle-box">
          <el-button type="primary" icon="edit" @click.stop="showSetupProblem"></el-button>
          <div class="search-wrapper">
            <search></search>
          </div>
        </div>
        <el-table :data="problemDatas" border ref="multipleTable" style="width: 80%"
                  @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="id" label="ID" width="150">
          </el-table-column>
          <el-table-column prop="title" label="" sortable width="350">
          </el-table-column>
          <el-table-column prop="tag" label="" sortable width="250" :formatter="calcTag">
          </el-table-column>
          <el-table-column prop="level" label="" sortable width="100">
          </el-table-column>
          <el-table-column prop="submitted" label="" sortable width="100">
          </el-table-column>
          <el-table-column prop="accepted" label="" sortable width="100">
          </el-table-column>
          <el-table-column label="" width="150">
            <template scope="scope">
              <el-button size="small"
                         @click.stop="handleEdit(scope.$index, scope.row)">
              </el-button>
              <el-button size="small" type="danger"
                         @click.stop="handleDelete(scope.$index, scope.row)">
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :total="100">
          </el-pagination>
        </div>
      </div>
    </transition>
    <transition name="el-fade-in-linear">
      <problem-edit v-show="isShowEdit" :isEdit="isEdit" :editTitle="editTitle" @editFinish="hideEdit"></problem-edit>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import { baseUrl, MSG_OK } from 'common/js/data'
  import axios from 'axios'
  import ElFormItem from '../../../node_modules/element-ui/packages/form/src/form-item'
  import Search from 'components/search/search'
  import ProblemEdit from 'components/manager/manage-problem-edit'
  import { mapActions } from 'vuex'
  import Problem from 'common/js/problem'

  export default{
    data(){
      return {
        problemDatas: [],
        cur_page: 1,
        multipleSelection: [],
        select_cate: '',
        select_word: '',
        del_list: [],
        is_search: false,
        rules: {
          id: [
            {required: true, message: 'id', trigger: 'blur'},
            {min: 3, max: 3, message: 'id3', trigger: 'blur'}
          ],
          title: [
            {required: true, message: '', trigger: 'blur'},
            {min: 5, message: '', trigger: 'blur'}
          ],
          description: [
            {required: true, message: '', trigger: 'blur'},
            {min: 5, message: '', trigger: 'blur'}
          ]
        },
        editTitle: '',
        isShowEdit: false,
        isEdit: false
      }
    },
    created(){
      this._getProblemsData(this.cur_page)
    },
    methods: {
      showSetupProblem(){
        this.editTitle = ''
        this.isEdit = false
        this.isShowEdit = true
      },
      handleCurrentChange(val){
        this.cur_page = val
        this._getProblemsData(this.cur_page)
      },
      _getProblemsData(curPage){
        let url = `${baseUrl}/problems?page=${curPage}`
        axios.get(url).then(response => {
          if (response.data.msg === MSG_OK) {
            this.problemDatas = response.data.result
          }
        }, response => {})
      },
      formatter(row, column){
        return row.address
      },
      filterTag(value, row){
        return row.tag === value
      },
      handleEdit(index, row){
        console.log(row)
        this.saveOneProblem(new Problem(row))
        this.editTitle = ''
        this.isEdit = true
        this.isShowEdit = true
      },
      handleDelete(index, row){
        //this.$message.error('' + (index + 1) + '')
        let url = `${baseUrl}/problems/${row.id}`
        axios.delete(url).then(response => {
          if (response.data.msg === 'ok') {
            this._getProblemsData(this.cur_page)
            this.$message({
              message: `:${row.title}`,
              type: 'success'
            })
          }
        }, response => {
          this.$message.error(`${row.title}`)
        })
      },
      delAll(){
        const self = this,
          length = self.multipleSelection.length
        let str = ''
        self.del_list = self.del_list.concat(self.multipleSelection)
        for (let i = 0; i < length; i++) {
          str += self.multipleSelection[i].name + ' '
        }
        self.$message.error('' + str)
        self.multipleSelection = []
      },
      calcTag(row, column, cellValue) {
        if (cellValue === '') {
          return 'Nothing'
        }
        return cellValue.replace(',', ' & ')
      },
      handleSelectionChange(val){
        this.multipleSelection = val
      },
      hideEdit(){
        this.isShowEdit = false
        this._getProblemsData(this.cur_page)
      },
      ...mapActions([
        'saveOneProblem'
      ])
    },
    computed: {
      data(){
        const self = this
        return self.contestDatas.filter(function (d) {
          let is_del = false
          for (let i = 0; i < self.del_list.length; i++) {
            if (d.name === self.del_list[i].name) {
              is_del = true
              break
            }
          }
          if (!is_del) {
            if (d.address.indexOf(self.select_cate) > -1 &&
              (d.name.indexOf(self.select_word) > -1 ||
              d.address.indexOf(self.select_word) > -1)
            ) {
              return d
            }
          }
        })
      }
    },
    components: {
      ElFormItem,
      Search,
      ProblemEdit
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .manage-problem
    .el-dialog
      .block
        .el-date-editor
          width 100%
    .handle-box
      margin-bottom 15px
      width 80%
      .search-wrapper
        float right

  .pagination
    margin: 20px 20% 0 0;
    text-align: right;
</style>



